<form class="row row-cols-lg-auto g-3 align-items-center" @submit.prevent="onSubmit" method="post"><div class="col-12"><label class="visually-hidden" for="inputExchange">Exchange</label><select class="form-select" id="inputExchange" v-model="exchange" @change="onExchangeChange"><option v-for="value in exchanges" :key="value.id" :value="value.id" :selected="exchange === value.id">{{ value.name }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputTicker">Ticker</label><select class="form-select" id="inputTicker" v-model="ticker"><option v-for="value in tickers" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputInterval">Interval</label><select class="form-select" id="inputInterval" v-model="interval"><option v-for="value in intervals" :key="value" :value="value">{{ value }}</option></select></div><div class="col-12"><label class="visually-hidden" for="inputIndicator">Indicator</label><select class="form-select" id="inputIndicator" v-model="indicator" @change="onExchangeChange"><option v-for="value in indicators" :key="value.id" :value="value.id" :selected="indicator === value.id">{{ value.name }}</option></select></div><div class="col-12"><button class="btn btn-primary" type="submit" :disabled="loading._">Submit</button></div></form><div class="mt-3 table-responsive"><component v-if="swingTrades.length" :is="tableComponent" :swingTrades="swingTrades"></component><div class="py-5 text-center" v-else><span v-if="fetched">No data has been retrieved.</span><span v-else>Please press the <strong>Submit</strong> button to retrieve the data.</span></div></div>